* {
    margin: 0px;
    padding: 0px;
}

body {
    margin: 15px;
}

#btn-container {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 40px;
    display: flex;
    margin-top: 15px;
    margin-bottom: 15px;
}
#text-search {
    
    margin-left: 10px;
    margin-right: 10px;
    justify-self: center;
    align-self: center;
    white-space: nowrap;
}
#datepicker {
    margin-left: 10px;
}
#btns-container {
    margin: 0;
    padding: 0;
    width: 50%;
    display: flex;
    justify-content: flex-end;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input[type=number] { -moz-appearance:textfield; }
#btn-refresh {
    margin-left: 50px;
}
#btn-searchtypes {
    width: 150px;
}
#input-buscar {
    min-width: 228px;
}
#bs-search {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 5px;
    margin-bottom: -10px;
    height: 40px;
}
#modal-header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
}
#alert-btn {
    margin: 10px;
}
#input-cedulabuscar {
    margin-left: 20px;
    width: 200px;
}
#tabla-registro {
    font-weight: 300;
    font-size: 15px;
}
#btn-edit {
    font-size: 13px;
}
#form-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}
#form-btn {
    justify-self: center;
    align-self: center;
    width: 100px;
}
#webcam-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#webcam {
    margin-bottom: 10px;
    border: 1px solid black;
}
#webcam-photo {
    margin-top: 10px;
    width: 140px;
    height: 160px;
    justify-self: center;
    align-self: center;
    border: 1px solid black;
    margin-bottom: 10px;
}

img,
video,
button {
  display: block;
}

input[type=radio] {
    margin-right: 5px;
    margin-top: -10px;
}